.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	padding-top: 28px;
	z-index: 100;
	height: 83px;
	color: $black;
	background-color: $alabaster;
	@include md {
		padding-top: 20px;
		height: 73px;
		color: $white; }
	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: 50%;
		margin-left: -50vw;
		width: 100vw;
		height: 100%;
		background-color: $alabaster; }
	&.is-fixed {
		position: fixed; }
	&[data-anim-from] {
		position: absolute; }
	&_anim {
		position: fixed;
		padding-top: 40px;
		color: $white;
		background-color: transparent;
		&:before {
			display: none; } }
	&__inner {
		position: relative;
		max-width: 1680px;
		@include md {
			padding: 0 20px; } }
	&__lang {
		@include md {
			position: absolute;
			bottom: 84px;
			left: 40px; } }
	&__share,
	&__mail {
		position: absolute;
		right: 30px;
		display: none;
		@include md {
			display: block; } }
	&__share {
		top: 240px;
		font-size: 20px; }
	&__mail {
		bottom: 88px;
		font-size: 14px; }
	&__wrap {
		@include md {
			position: absolute;
			top: -20px;
			right: 0;
			width: 100vw;
			z-index: 200;
			opacity: 0;
			visibility: hidden;
			padding: 120px 60px 160px 40px;
			min-height: 100vh;
			overflow: auto;
			transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
			background-image: linear-gradient(140deg, #2D3353 0%, #3E3E88 81%);
			&.is-open {
				opacity: 1;
				visibility: visible; } } }
	&__logo {
		transform: translate3d(0,0,0);
		transition: height .3s;
		.js-header-main.is-fixed & {
			@include md {
				height: 34px; } } } }

.logo {
	position: relative;
	float: left;
	z-index: 201;
	@include md {
		height: 70px; }
	svg,
	img {
		height: 100%;
		width: auto;
		display: block; }
	svg,
	svg g {
		fill: $heavyMetal;
		transition: fill .3s ease-in-our;
		.is-active & {
			@include md {
				fill: $white; } }
		.header_anim & {
			fill: $white; } } }
.nav {
	float: left;
	margin-left: 167px;
	font-size: 16px;
	font-weight: 700;
	@include lg {
		margin-left: 130px; }
	@include md {
		margin-left: 0;
		font-size: 35px;
		line-height: 1.1; }
	&__item {
		vertical-align: top;
		display: inline-block;
		@include md {
			display: block; }
		&:not(:first-child) {
			margin-left: 63px;
			@include md {
				margin: 27px 0 0; } }
		&.is-active {
			.nav__link {
				opacity: 1; } } }
	&__link {
		opacity: .6;
		color: $black;
		transition: opacity .3s ease-in-out;
		@include md {
			color: $white; }
		@include hover {
			opacity: 1; }
		&.is-active {
			opacity: 1; }
		.header_anim & {
			color: $white; } } }
.lang {
	position: relative;
	float: right;
	padding-right: 12px;
	min-width: 12px;
	font-size: 16px;
	font-weight: 700;
	text-align: right;
	cursor: pointer;
	user-select: none;
	@include md {
		padding-right: 30px;
		font-size: 35px;
		line-height: 1.1; }
	&.is-active {
		&:before {
			transform: rotate(180deg); }
		.lang__list {
			opacity: 1;
			visibility: visible;
			transform: translateY(0); } }
	&:before {
		position: absolute;
		content: "";
		top: 50%;
		right: 0;
		margin-top: -2px;
		@include arr(7,4,$black,b);
		.header_anim & {
			@include arr(7,4,$white,b);
			@include md {
				@include arr(18,10,$white,b); } }
		@include md {
			margin-top: -5px;
			@include arr(18,10,$white,b); } }
	&__current {
		position: relative;
		z-index: 1;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			right: -12px;
			bottom: 0;
			@include md {
				right: -40px; } } }
	&__list {
		position: absolute;
		top: 100%;
		width: 200px;
		margin-top: -2px;
		right: 0;
		opacity: 0;
		transform: translateY(8px);
		visibility: hidden;
		transition: opacity 0.1s ease-in, visibility 0.1s ease-in, transform 0.1s ease-in;
		@include md {
			margin: 0 0 12px; }
		.header__wrap & {
			@include md {
				top: auto;
				right: auto;
				left: 0;
				bottom: 100%;
				text-align: left;
				transform: translateY(-8px); } } }
	&__item {
		margin-top: 10px;
		line-height: 1.1;
		display: block;
		a {
			color: $black;
			@include md {
				color: $white; }
			.header_anim & {
				color: $white; } } } }

.btn-open-nav {
	position: relative;
	float: right;
	z-index: 201;
	width: 31px;
	height: 31px;
	background-color: transparent;
	display: none;
	@include md {
		display: block; }
	&.is-active {
		span {
			border-color: $white;
			&:nth-child(1) {
				transform: rotate(45deg) translate3d(6px,-3px,0); }
			&:nth-child(2) {
				opacity: 0;
				transform: scaleX(0); }
			&:nth-child(3) {
				transform: rotate(-45deg) translate3d(7px,2px,0); } } }
	span {
		position: absolute;
		left: 0;
		width: 31px;
		border-top: 4px solid $black;
		opacity: 0.87;
		transition: width 0.3s ease-in-out, opacity 0.3s ease-in-out, transform 0.3s ease-in-out, border-color 0.3s;
		.header_anim & {
			border-color: $white; }
		&:nth-child(1) {
			top: 0;
			transform-origin: left top; }
		&:nth-child(2) {
			top: 14px;
			transform-origin: left center; }
		&:nth-child(3) {
			bottom: 0;
			transform-origin: left bottom; } } }
