.client {
	padding-top: 254px;
	@include lg {
		padding-top: 130px; }
	@include sm {
		padding-top: 86px; }
	&__title {
		margin-bottom: 78px;
		@include sm {
			padding: 0 20px;
			margin-bottom: 34px; } }
	&__container {
		position: relative;
		display: block;
		overflow: hidden;
		height: 778px;
		@include vertical;
		@include lg {
			height: 500px; }
		@include sm {
			height: 324px; } }
	&__bg {
		position: absolute;
		top: -100px;
		left: -100px;
		right: -100px;
		bottom: -100px;
		background-image: linear-gradient(151deg, #0A2F2A 8%, #2D5442 100%);
		transform: translate3d(0,0,0);
		transition: transform 0.25s; }
	&__logo {
		position: relative;
		z-index: 1;
		display: inline-block;
		vertical-align: middle;
		transform: translate3d(0,0,0) scale(1);
		transition: transform 0.25s;
		@include hover {
			transform: translate3d(0,0,0) scale(1.1);
			~ .client__bg {
				transform: translate3d(-100px,-100px,0); } }
		.icon {
			fill: $white;
			font-size: 203px;
			@include sm {
				font-size: 96px; } } } }
