.section {
	position: relative;
	padding-bottom: 192px;
	@include lg {
		padding: 50px 0; }
	@include md {
		padding: 21px 0 20px; }
	&:first-child {
		padding-top: 15px; }
	&:last-child {
		padding-bottom: 78px; }
	&:first-child,
	&:last-child {
		@include lg {
			padding-top: 50px;
			padding-bottom: 50px; }
		@include sm {
			padding-top: 21px;
			padding-bottom: 20px; } }
	&__header,
	&__description {
		position: relative;
		z-index: 2;
		width: 380px;
		@include md {
			padding-left: 20px;
			padding-right: 20px; } }
	&__header {
		@include lg {
			margin-bottom: -80px; }
		@include md {
			width: 100%;
			max-width: 420px; }
		@include sm {
			margin-bottom: -45px;
			max-width: 280px; } }
	&__description {
		@include lg {
			clear: both;
			padding-top: 41px;
			width: 100%; }
		p {
			@include f(18px, 400, 28px);
			&:not(:last-child) {
				margin-bottom: 28px; }
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				color: #0171BB;
				@include hover {
					&:before {
						opacity: 0; } }
				&:before {
					content: '';
					position: absolute;
					left: 0;
					right: 0;
					bottom: 2px;
					border-bottom: 1px solid #0171BB;
					transition: opacity 0.25s; } } } }
	&__figure {
		position: absolute;
		top: 0;
		left: 420px;
		right: 0;
		bottom: 0;
		background: $lightGray;
		@include vertical;
		@include lg {
			position: relative;
			top: auto;
			left: auto;
			right: auto;
			bottom: auto;
			float: right;
			padding-bottom: 66%;
			width: 72%;
			height: 0; }
		@include md {
			padding-bottom: 50%;
			width: 60%; }
		@include sm {
			padding-bottom: 66%;
			width: 72%; } }
	&__label {
		margin-bottom: 16px;
		@include sm {
			margin-bottom: 8px; } }
	&__title {
		margin-bottom: 35px;
		@include lg {
			margin: 0; } }
	&__trust {
		top: 47%;
		[data-svg-wrapper] {
			position: relative;
			display: inline-block;
			vertical-align: top;
			width: 190px;
			@include lg {
				max-width: 32%; } }
		[data-svg-container] {
			position: absolute;
			top: 50%;
			left: 50%;
			overflow: hidden;
			width: 46%;
			transform: translate3d(-42%,-56%,0); }
		[data-svg-icon] {
			width: 100%; }
		[data-svg-icon="shield"] {
			opacity: 0;
			transform: translate3d(0,20%,0); }
		[data-svg-icon="check"] {
			opacity: 0.3;
			transform: translate3d(0,100%,0); } }
	&__experience {
		top: 39.5%;
		img {
			width: 498px;
			max-width: 90%;
			@include lg {
				max-width: 61%; } } }
	&__approach {
		top: 37.5%;
		[data-svg-wrapper] {
			position: relative;
			display: inline-block;
			vertical-align: top;
			width: 293px;
			@include lg {
				max-width: 43%; } }
		[data-svg-inner] {
			position: relative;
			padding-bottom: 101.5%;
			width: 100%;
			height: 0; }
		[data-svg-icon="target"] {
			position: absolute;
			left: 0;
			bottom: 0;
			width: 81%;
			opacity: 0;
			transform: translate3d(0,20%,0); }
		[data-svg-icon="arrow"] {
			width: 100%; }
		[data-svg-container] {
			position: absolute;
			top: -8%;
			right: 0;
			overflow: hidden;
			padding-bottom: 82%;
			width: 63%;
			height: 0;
			opacity: 0;
			transform: translate3d(18%,-12%,0) rotate(45deg); }
		[data-svg-box] {
			position: absolute;
			top: 6.5%;
			left: 0;
			width: 100%;
			transform: translate3d(0,0,0) rotate(-45deg); } }
	&__icon {
		position: absolute;
		left: 0;
		right: 0;
		font-size: 0;
		text-align: center;
		transform: translate3d(0,-50%,0);
		@include lg {
			top: 50%; } }
	[data-animation-section-item] {
		opacity: 0;
		transform: translate3d(0,40px,0); } }

.sections {
	border-bottom: 1px solid rgba($black, 0.2);
	@include md {
		padding-bottom: 41px; } }
